  componentDidMount() {
    axios
      .get(BASE_URL + 'seller/product', { withCredentials: true })
      .then(response => {
        this.setState(
          {
            response: response.data,
          },
          () => {
            this.getProducts();
          },
        );
      })
      .catch(error => {
        console.log('Error fetching products');
        console.log(error);
        Alert.error(
          error.response
            ? error.response.data.message
            : 'Unable to connect with the server, please check your internet connection.',
          {
            position: 'top-right',
            effect: 'slide',
            offset: 55,
          },
        );
      });
    this.getProducts();
  }
